import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';

const AnyReactComponent = ({ text }) => (
  <div
    style={{
      color: 'white',
      background: 'grey',
      padding: '15px 10px',
      display: 'inline-flex',
      textAlign: 'center',
      alignItems: 'center',
      justifyContent: 'center',
      borderRadius: '100%',
      transform: 'translate(-50%, -50%)',
    }}
  >
    {text}
  </div>
);

class SimpleMap extends Component {
  render() {
    return (
      // Important! Always set the container height explicitly
      <div style={{ height: '650px', width: '650px' }}>
        <GoogleMapReact
          bootstrapURLKeys={{
            key: 'AIzaSyCnf8UxHXIr51woS63QVR5Mi-IZRO9hrsg',
          }}
          defaultCenter={{
            lat: 37.573957,
            lng: 126.936834,
          }}
          defaultZoom={14}
        >
          <AnyReactComponent
            lat={37.573957}
            lng={126.936834}
            text="Dari Plan"
          />
        </GoogleMapReact>
      </div>
    );
  }
}

export default SimpleMap;
